}

@font-face {
  font-display: swap;
  font-family: Space Grotesk;
  font-style: normal;
  font-weight: 700;
  src: url("space-grotesk-v15-latin-700.c2d72cb0.woff2") format("woff2"), url("space-grotesk-v15-latin-700.aa2f46b7.ttf") format("truetype");
}

* {
  margin: 0;
  padding: 0;
}

*, :before, :after {
  box-sizing: border-box;
}

@keyframes smoothScrollDocument {
  from, to {
    scroll-behavior: smooth;
  }
}

@keyframes smoothScrollPage {
  from, to {
    scroll-behavior: smooth;
  }
}

html {
  font-family: var(--ff-Space-Grotesk-sans-serif);
  -webkit-text-size-adjust: none;
  -moz-text-size-adjust: none;
  text-size-adjust: none;
  scrollbar-color: var(--green) var(--gray);
  scrollbar-gutter: stable;
  scrollbar-width: thin;
  animation: 1s smoothScrollDocument;
  overflow-x: hidden;
  overflow-y: scroll;
}

html:focus-within {
  scroll-behavior: smooth;
  animation-name: smoothScrollPage;
}

html::-webkit-scrollbar {
  width: 10px;
}

html::-webkit-scrollbar-thumb {
  background-color: var(--green);
  border-radius: 12px;
}

html::-webkit-scrollbar-track {
  background-color: var(--gray);
  border-radius: 9px;
}

img:not(.background-image, .icon-error) {
  display: var(--block);
  width: 100%;
  height: auto;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

body {
  background-color: var(--black);
  line-height: var(--line-height-normal);
  scrollbar-width: none;
  width: 100%;
  min-height: 100vh;
  overflow: hidden;
}

.sr-only {
  white-space: nowrap;
  border-width: var(--sr-only-border-width);
  width: var(--sr-only-width);
  height: var(--sr-only-height);
  left: var(--sr-only-left);
  top: var(--sr-only-top);
  margin: var(--sr-only-margin);
  padding: var(--sr-only-padding);
  position: absolute;
  overflow: hidden;
}

form {
  font-size: var(--fs-1);
  font-style: normal;
  font-weight: var(--fw-500);
  position: relative;
}

input, textarea {
  border: none;
  border-bottom: 1px solid var(--white);
  color: var(--white);
  font: inherit;
  line-height: var(--line-height-loose);
  background-color: #0000;
  width: 100%;
  padding-inline-start: var(--gap-15);
  display: block;
}

textarea {
  resize: none;
}

input:focus, textarea:focus {
  border-bottom: 1px solid var(--green);
  outline: none;
}

input::placeholder, textarea::placeholder {
  opacity: .5;
  font: inherit;
  text-transform: uppercase;
  color: inherit;
}

ul, ul[role="list"] {
  list-style-type: none;
}

a {
  color: var(--white);
  font-style: normal;
  text-decoration: none;
}

div[class~="section-container"], .nav {
  width: min(90%, 1150px);
  margin-inline: auto;
}

body {
  position: relative;
}

header {
  z-index: 2;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.nav {
  padding-top: var(--space-4-6);
  display: var(--flex);
  gap: var(--gap-12_5);
  flex-direction: column;
}

.nav > p {
  text-align: center;
  font-weight: var(--fw-700);
  font-size: var(--fs-2);
  line-height: var(--line-height-snug);
}

.nav > p > a {
  transition: color .4s ease-in;
}

.nav svg > path {
  transition: fill .4s ease-in;
}

@media (hover: hover) {
  .nav > p > a:hover {
    color: var(--green);
  }

  .nav > ul a:hover > svg > path {
    fill: var(--green);
  }
}

.nav > ul[class~="profiles"] {
  display: var(--flex);
  justify-content: center;
  align-items: center;
  gap: var(--gap-12_5);
}

@media only screen and (min-width: 48em) {
  .nav {
    flex-direction: row;
    justify-content: space-between;
  }
}

section[class~="hero"] {
  background-image: url("pattern-rings.be49833a.svg");
  background-position: -22rem 7.7rem;
  background-repeat: no-repeat;
}

div[class~="hero-container"] {
  display: var(--flex);
  gap: var(--gap-22_5);
  flex-direction: column;
}

div[class~="hero-container"] > figure {
  padding-top: var(--gap-50);
  background-color: var(--dark-grey);
  margin-inline: auto;
  position: relative;
}

img[class~="adamkeyes-image"] {
  width: 174px;
  max-width: 100%;
}

div[class~="hero-container"] > figure:after {
  content: url("pattern-circle.ad1ffe0a.svg");
  position: absolute;
  bottom: -1%;
  left: 124%;
}

.introduction-heading {
  text-align: center;
  color: var(--white);
  font-weight: var(--fw-700);
  font-size: var(--fs-4);
  line-height: var(--line-height-none);
  margin-bottom: var(--gap-15);
}

.introduction-heading-name {
  display: var(--inline-block);
  position: relative;
}

.introduction-heading-name:after {
  --underline-height-mobile: .25rem;
  --underline-height-desktop: .375rem;
  content: "";
  display: var(--block);
  background-color: var(--green);
  padding-block: var(--underline-height-mobile);
  width: 100%;
}

.introduction-text {
  text-align: center;
  color: var(--gray);
  font-size: var(--fs-1);
  font-weight: var(--fw-500);
  line-height: var(--line-height-relaxed);
  margin-bottom: var(--gap-15);
}

p[class="introduction-contact-me"] {
  text-align: center;
}

a[href="#contact-me"] {
  font-weight: var(--fw-700);
  font-size: var(--fs-1);
  font-style: normal;
  line-height: var(--line-height-relaxed);
  letter-spacing: var(--letter-spacing-widest);
  text-transform: uppercase;
  display: var(--inline-block);
  transition: color .4s ease-in-out;
  position: relative;
}

@media (hover: hover) {
  a[href="#contact-me"]:hover {
    color: var(--green);
  }
}

a[href="#contact-me"]:after {
  content: "";
  background-color: var(--green);
  width: 100%;
  padding-block: .125rem;
  display: block;
  position: absolute;
}

@media only screen and (min-width: 48em) {
  section[class~="hero"] {
    background-position: -22rem 7.7rem;
  }

  div[class~="hero-container"] {
    flex-direction: row;
    gap: 0;
    position: relative;
  }

  div[class~="hero-container"] > figure {
    z-index: -1;
    background-color: unset;
    padding-top: unset;
    order: 2;
    position: absolute;
    top: 0;
    right: -10%;
  }

  img[class~="adamkeyes-image"] {
    width: 336px;
  }

  div[class~="hero-container"] > figure:after {
    left: 70%;
  }

  .introduction-heading {
    text-align: left;
    margin-bottom: var(--gap-40);
    width: 70.125%;
    padding-top: 6.9rem;
    font-size: 4.5rem;
  }

  .introduction-heading__text {
    display: var(--block);
  }

  .introduction-text {
    text-align: left;
    margin-bottom: var(--gap-20);
    width: 61.25%;
  }

  .introduction-heading-name:after {
    padding-block: var(--underline-height-desktop);
  }

  .introduction-heading-text {
    display: var(--block);
  }

  p[class="introduction-contact-me"] {
    text-align: left;
  }
}

@media only screen and (min-width: 90em) {
  section[class~="hero"] {
    background-position: -9rem 7.7rem;
  }

  div[class~="hero-container"] > figure {
    right: 0;
  }

  img[class~="adamkeyes-image"] {
    width: 445px;
  }

  div[class~="hero-container"] > figure:after {
    left: unset;
    bottom: 9%;
    right: 84%;
  }

  .introduction-heading {
    padding-top: var(--gap-120);
    margin-bottom: var(--gap-27_5);
    font-feature-settings: "clig" off, "liga" off;
    font-size: 5.5rem;
  }

  .introduction-text {
    margin-bottom: var(--gap-40);
    width: 39.25%;
  }

  .introduction-heading__text {
    display: var(--inline);
  }

  nav > ul[class~="profiles"] {
    margin-right: 1.84rem;
  }
}

section[class~="skills"] {
  margin-top: var(--gap-50);
  position: relative;
}

div[class~="skills-container"] {
  border-top: 1px solid var(--white);
  border-bottom: 1px solid var(--white);
  padding: 2.5rem 0;
  position: relative;
}

div[class~="skills-container"]:after {
  content: url("pattern-rings.be49833a.svg");
  position: absolute;
  bottom: -10%;
  left: 49%;
}

ul[class="skills-list"] {
  display: var(--grid);
  justify-content: center;
  row-gap: var(--gap-15);
  grid-template-rows: 1fr;
}

li[class^="skills-list_"] {
  text-align: center;
  display: var(--flex);
  flex-direction: column;
  gap: .06rem;
}

li[class^="skills-list_"] > .skill {
  color: var(--white);
  font-weight: var(--fw-700);
  font-style: normal;
  font-size: var(--fs-3);
  line-height: var(--line-height-relaxed);
}

li[class^="skills-list_"] > .experience {
  color: var(--gray);
  font-weight: var(--fw-500);
  font-size: var(--fs-1);
  line-height: var(--line-height-loose);
}

@media only screen and (min-width: 48em) {
  section[class~="skills"] {
    margin-top: var(--gap-40);
  }

  div[class~="skills-container"]:after {
    bottom: -4%;
    left: 78%;
  }

  div[class~="skills-container"] {
    border-bottom: unset;
    padding: 3.25rem 0;
  }

  ul[class="skills-list"] {
    grid-template-columns: repeat(2, 1fr);
    justify-content: start;
    column-gap: .44rem;
  }

  li[class^="skills-list_"] {
    text-align: left;
    gap: .88rem;
  }
}

@media only screen and (min-width: 90em) {
  section[class~="skills"] {
    margin-top: var(--gap-130);
  }

  div[class~="skills-container"]:after {
    bottom: -4%;
    left: 90%;
  }

  ul[class="skills-list"] {
    grid-template-columns: repeat(3, 1fr);
    justify-content: start;
    column-gap: 1.87rem;
  }
}

section[class~="projects"] {
  margin-top: var(--space-8-9);
}

div[class~="projects-container"] {
  margin-bottom: var(--gap-60);
}

div[class~="projects-container"] > div {
  margin-bottom: var(--gap-27_5);
  justify-content: space-between;
  align-items: center;
  display: flex;
}

h2[class="projects-heading"] {
  color: var(--white);
  font-weight: var(--fw-700);
  font-style: normal;
  font-size: var(--fs-4);
}

p[class="project-name"] {
  color: var(--white);
  text-transform: uppercase;
  font-size: var(--fs-2);
  font-style: normal;
  font-weight: var(--fw-700);
  margin-top: var(--gap-12_5);
  margin-bottom: var(--gap-3_75);
}

ul[class="project-tech-stack"] {
  color: var(--gray);
  margin-bottom: var(--gap-12_5);
  font-feature-settings: var(--font-feature-settings);
  font-weight: var(--fw-500);
  font-style: normal;
  font-size: var(--fs-1);
  display: var(--flex);
  align-items: center;
  gap: var(--gap-12_5);
}

div[class="project-links"] {
  display: var(--flex);
  align-items: center;
  gap: var(--gap-20);
}

a[href="#"] {
  font-weight: var(--fw-700);
  font-size: var(--fs-1);
  font-style: normal;
  line-height: var(--line-height-relaxed);
  letter-spacing: var(--letter-spacing-widest);
  text-transform: uppercase;
  display: var(--inline-block);
  transition: color .4s ease-in-out;
  position: relative;
}

@media (hover: hover) {
  a[href="#"]:hover {
    color: var(--green);
  }
}

a[href="#"]:after {
  content: "";
  background-color: var(--green);
  width: 100%;
  padding-block: .125rem;
  display: block;
  position: absolute;
}

ul[class="projects-list"] {
  grid-template-columns: 1fr;
  gap: 2.5rem 0;
  display: grid;
}

@media only screen and (min-width: 48em) {
  ul[class="projects-list"] {
    grid-template-columns: repeat(2, 1fr);
    gap: 3.7rem 1.25rem;
  }
}

@media only screen and (min-width: 90em) {
  ul[class="projects-list"] {
    gap: 4.31rem 1.88rem;
  }

  ul[class="projects-list"] > li {
    position: relative;
  }

  div[class~="projects-container"] {
    margin-bottom: var(--gap-90);
  }

  @media (hover: hover) {
    div[class="project-links"] {
      background-color: var(--blur);
      justify-content: center;
      gap: var(--gap-30);
      opacity: 0;
      visibility: hidden;
      flex-direction: column;
      transition-property: opacity;
      transition-duration: .6s;
      transition-timing-function: ease-out;
      position: absolute;
      inset: 0 0 22%;
    }

    li[class^="projects-list__"]:hover > div[class="project-links"] {
      opacity: 1;
      visibility: visible;
      cursor: pointer;
    }

    div[class="project-links"] > * {
      transition-property: transform;
      transition-duration: .6s;
      transition-timing-function: ease-out;
      transform: translateY(50px);
    }

    li[class^="projects-list__"]:hover > div[class="project-links"] > * {
      transform: translateY(0);
    }
  }
}

section[class~="contact"] {
  background-color: var(--dark-grey);
  background-image: url("pattern-rings.be49833a.svg");
  background-position: left -21.25rem bottom 5.06rem;
  background-repeat: no-repeat;
}

div[class~="contact-container"] {
  padding-top: var(--space-7-8);
  padding-bottom: var(--gap-60);
  border-bottom: 1px solid var(--white);
  display: var(--flex);
  gap: var(--gap-30);
  flex-direction: column;
}

div[class^="form__"] {
  margin-bottom: var(--gap-20);
  position: relative;
}

div[class^="form__"] > p {
  text-align: right;
  margin-top: var(--gap-3_75);
  font-style: normal;
  font-weight: var(--fw-500);
  font-size: .75rem;
  display: block;
}

div[class^="form__"] > img {
  position: absolute;
  top: 8px;
  right: 1px;
}

img[class~="icon-error__show"] {
  opacity: 1;
  visibility: visible;
  transition: opacity .45s ease-out;
}

img[class~="icon-error__hide"] {
  opacity: 0;
  visibility: hidden;
}

input[aria-invalid="true"], textarea[aria-invalid="true"] {
  border-bottom: 1px solid var(--red);
}

:is(input[aria-invalid="true"] ~ p, textarea[aria-invalid="true"] ~ p) {
  color: var(--red);
}

.btn__submit-form {
  -webkit-appearance: none;
  appearance: none;
  color: var(--white);
  text-transform: uppercase;
  font: inherit;
  line-height: var(--line-height-relaxed);
  letter-spacing: var(--letter-spacing-widest);
  cursor: pointer;
